<template>
  <div>
    <el-button type="text" @click="dialogTableVisible = true">设置投放地区</el-button>

    <el-dialog title="设置投放地区" :visible.sync="dialogTableVisible">

      <el-row class="el-row">
        <el-switch v-model="checkAll" active-text="全选" inactive-text="全不选" @change="checkAllArea">
        </el-switch>
      </el-row>
      <template v-for="(item,index) in cityOptionsArea">

        <el-row class="el-row">
          <el-col :span="6">
            <el-checkbox v-model="cityOptionsAreaCheckAll[index]" :label="item.area" @change="handleCheckAllChange(index)"></el-checkbox>

          </el-col>
          <el-col :span="18">

            <el-checkbox-group v-model="cityOptionsAreaChecked[index]">
              <el-checkbox v-for="p in item.prov" :label='p' @change="handleCheckedCitiesChange(index)"> {{p.name}}</el-checkbox>
            </el-checkbox-group>

          </el-col>


        </el-row>

      </template>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogTableVisible = false">取 消</el-button>
        <el-button type="primary" @click="savePostFreeArea">确 定</el-button>
      </div>
    </el-dialog>


  </div>



</template>

<script>
  const cityOptionsArea = [{
      area: '华北',
      prov: [{area_id:19,name:'北京'}, {area_id:461,name:'天津'}, {area_id:125,name:'河北'}, {area_id:393,name:'山西'}, {area_id:333,name:'内蒙古'}]
    },
    {
      area: '东北',
      prov: [{area_id:294,name:'辽宁'}, {area_id:234,name:'吉林'}, {area_id:165,name:'黑龙江'}]
    },
    {
      area: '华东',
      prov: [{area_id:417,name:'上海'}, {area_id:255,name:'江苏'}, {area_id:508,name:'浙江'}, {area_id:39,name:'福建'}, {area_id:1,name:'安徽'}, {area_id:368,name:'山东'}]
    },
    {
      area: '华中',
      prov: [{area_id:145,name:'河南'}, {area_id:184,name:'湖北'}, {area_id:212,name:'湖南'}, {area_id:279,name:'江西'},]
    },
    {
      area: '华南',
      prov: [{area_id:68,name:'广东'}, {area_id:92,name:'广西'}, {area_id:120,name:'海南'}]
    },
    {
      area: '西南',
      prov: [{area_id:532,name:'重庆'}, {area_id:438,name:'四川'}, {area_id:109,name:'贵州'}, {area_id:488,name:'云南'},{area_id:463,name:'西藏'}]
    },
    {
      area: '西北',
      prov: [{area_id:406,name:'陕西'}, {area_id:52,name:'甘肃'}, {area_id:357,name:'青海'}, {area_id:351,name:'宁夏'}, {area_id:471,name:'新疆'}]
    },
    {
      area: '其他地区',
      prov: [{area_id:578,name:'中国台湾'}, {area_id:599,name:'中国香港'}, {area_id:576,name:'中国澳门'}, {area_id:574,name:'国外'}]
    },

  ];
  export default {
    
    props: {
      act_index: {
        type:Number
      }
    },


    data() {
      return {
        
        
        dialogTableVisible: false,

        checkAll: true,

        checkedCities: ['上海', '北京'],
        cityOptionsArea: cityOptionsArea,
        cityOptionsAreaChecked: [],
        cityOptionsAreaCheckAll: [],
        isIndeterminate: true

      }

    },

    methods: {

      handleCheckAllChange(index) {

        this.cityOptionsAreaChecked[index] = this.cityOptionsAreaChecked[index].length > 0 ? [] : cityOptionsArea[index]
          .prov;
        this.cityOptionsAreaCheckAll[index] = this.cityOptionsAreaChecked[index].length > 0 ? true : false;
      },
      handleCheckedCitiesChange(index) {

        this.cityOptionsAreaCheckAll[index] = this.cityOptionsAreaChecked[index].length == cityOptionsArea[index].prov.length;

      },
      checkAllArea(a) {
        console.log(a);
        if (a) {
          for (let i = 0; i < cityOptionsArea.length; i++) {
            this.cityOptionsAreaChecked[i] = this.cityOptionsArea[i].prov;
            this.cityOptionsAreaCheckAll[i] = true;
          }
        } else {
          for (let i = 0; i < cityOptionsArea.length; i++) {
            this.cityOptionsAreaChecked[i] = [];
            this.cityOptionsAreaCheckAll[i] = false;
          }

        }

      },
      savePostFreeArea()
      {
        this.dialogTableVisible = false;
        
        console.log(this.cityOptionsAreaChecked);
        

        this.$emit('savePostFreeArea',this.cityOptionsAreaChecked );

      }

    },
    mounted() {
      this.checkAllArea(1);

    }
  }
</script>

<style scoped>
  .el-row {
    line-height: 1.5rem;
  }
</style>
