<template>
  <div>
    <h1>创建地域模板</h1>

    <div>
      <SetPostFreeArea @savePostFreeArea="getPostFreeArea"></SetPostFreeArea>

    </div>

    <div>
      <el-form :model="tplForm" :rules="rules" ref="tplForm">
        <el-form-item prop="name">模板名称：<el-input v-model="tplForm.name"></el-input>
        </el-form-item>
        <el-button @click="submitForm('tplForm')" type="primary" :loading="tplForm.submitBtnLoading">立即创建</el-button>
        <el-button @click="resetForm('tplForm')">重置</el-button>
      </el-form>
    </div>

  </div>
</template>

<script>
  import {
    request
  } from "@/network/request.js";
  import SetPostFreeArea from "@/components/commen/SetPostFreeArea.vue";
  import Qs from 'qs';


  export default {
    data() {
      return {
        area_ids: [],
        tplForm: {
          name: "",
          submitBtnLoading: false,
        },
        rules: {
          name: [{
              required: true,
              message: '请输入模板名称',
              trigger: 'blur'
            },
            {
              min: 3,
              max: 15,
              message: '长度在 3 到 15 个字符',
              trigger: 'blur'
            }
          ],
        }

      }
    },
    components: {
      SetPostFreeArea: SetPostFreeArea,
    },

    methods: {

      submitForm(formName) {

        this.tplForm.submitBtnLoading = true;

        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(valid);

            let data = {
              area_ids: this.area_ids,
              name: this.tplForm.name
            }

            request({
              url: "/subway/SaveAreaTpl",
              method: 'post',
              data: Qs.stringify(data),

            }).then(res => {

              console.log(res)

              this.$message({
                showClose: true,
                message: '保存成功',
                type: 'success'
              });


              this.$router.push({
                path: "/bat/areaApply",
                query: {
                  id: 1
                }
              })


            })

          } else {
            return false;
          }


        });

        this.tplForm.submitBtnLoading = false;
      },
      getPostFreeArea(area_ids) {

        this.area_ids = area_ids;

      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }

    }
  }
</script>

